import WebsiteLayout from "../../components/websiteLayout";
import { useRouter } from 'next/router';

import fetch from 'isomorphic-unfetch';

const Lug = (props) => {
    const { query } = useRouter();

    return (
        <WebsiteLayout>
            <div id="single-lug" className="container">
                <h1>لاگ {query.id}</h1>

                <div className="row">
                    <div className="col-md-3 col-xs-12">
                        <img className="full-img" src={props.lug.pic} />
                    </div>
                    <div className="col-md-8 col-xs-12">
                        <h5 className="card-title">{"لاگ " + props.lug.id}</h5>
                        <p className="card-text">{props.lug.desc}</p>
                        <small className="text-muted">{props.lug.date}</small>
                    </div>
                </div>


            </div>
        </WebsiteLayout>
    )
};

Lug.getInitialProps = async function (context) {
    const { id } = context.query;
    const res = await fetch(`https://apdr.ir/api/lug/${id}`);
    const data = await res.json();

    return { lug: data.lug, sections: data.sections };
};

export default Lug;